@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.day-selector {
    display: flex;
    flex-direction:row;
    flex-wrap:nowrap;
    width: 100%;
    justify-content: space-around;
    font-size: 1.2vw;  
    padding: 0 1vw;
    box-sizing:border-box;
    @include mobile-only {
        flex-direction: row;
        flex-wrap:wrap;
        justify-content: space-around;


    }

    &__day {
        display:flex;
        flex-direction:row;
        padding: 0.2vw 0.1vw;
        color: white;
        width: 15%;
        text-align:center;
        box-sizing:border-box;
        border-radius: 0.3vw;
        align-items:center;
        @include mobile-only {
        width: 33.3%;
        font-size: 2vh;
        margin-bottom: 0vh;
        flex-wrap:nowrap;
        justify-items:space-between;
        padding: 0;
        overflow:hidden;
        margin-bottom: 0.5vh;
        border-radius:0;
        }
        &:hover {
            transform: scale(1.1);
            @include mobile-only {
                transform: none;
            }
        }
      &__of-week {
          background-color:white;
          color: rgb(0, 98, 255);
          width: 80%;
          height: min-content;
          border-radius:0.5vw;
 

          box-shadow: 2px 2px 2px white;
          @include mobile-only {
              font-size: 2vh;
              width: available;
              border:none;
              height:3.5vh;
              display:flex;
              flex-direction:row;
              align-items:center;
              padding-left: 1vh;
              border-radius:0;
              text-align:right;

          }
          &--current {
            background-color:rgb(2, 81, 112);
            color: white;
            @include mobile-only {
                margin-bottom: 0;

                border:none;
            }

        }
      }
      &__of-month {
            width:2.5vw;
            height:2.5vw;
          background-color: rgb(0, 153, 255);
          border-radius: 50%;
          font-size:1.5vw;
          display:flex;
          flex-direction:row;
          align-items:center;
          justify-content:center;
          margin-left:-1vw;

          box-shadow: 2px 2px 2px white;
          @include mobile-only {
            font-size: 2.5vh;
            width:3.5vh;
            height:3.5vh;
            border-radius: 0;
            box-shadow: none;
        }
          &--current {
            background-color:rgb(255, 187, 0);

            @include mobile-only {
                margin-bottom: 0;
                border:none;
            }

        }
      }
      
    }
}