@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";


.detailed-forecast {
    width: 35%;
    justify-content:center;
    display:flex;
    flex-direction:row;
    flex-wrap:wrap;
    height: max-content;
    margin-right: 2vw;

    @include mobile-only {
        position: fixed;
        bottom:0;
        width: 100%;
        background-color: rgba(0, 5, 26, 0.863);
        z-index:99;
        box-shadow: 0 -3px 2px rgba(0, 32, 177, 0.856);
    }
    &__data-container {

        margin-right: 2vw;
        display:flex;
        flex-direction:row;
        align-items: center;
        justify-content:center;

        border-radius: 0.1vw;
        overflow:hidden;
        font-size:1vw;
        color:white;
        
        @include mobile-only {
            margin:0;
            font-size: 2vh;
            width: 100%;
            box-shadow:none;
            border-bottom: 1px solid rgb(255, 255, 255);
        }
    }
    &__time {

        font-size: 1.4vw;
        padding: 2vw 0;
        width: 20vw;
        @include mobile-only {
            font-size: 2vh;
            width: 60vw;
        }
    }
    &__data {
        width: 50vw;

        text-align:left;
        padding: 0.5vw;
        font-weight: bold;
        color: rgb(0, 234, 255);
        white-space:nowrap;


        
    }
    &__data-label {
        width: 50vw;
        text-align:right;
        height: 100%;
        padding: 0.5vw;
        border-right: 1px solid orangered;



        color:white;
        box-sizing:border-box;
        @include mobile-only {
            background-color: transparent;
        }

    }
}