@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

body {

    background: rgb(35,64,226);
    background: url("../images/dark_sky.jpg");
    background-position:center;
    overflow-x:hidden;
    background-size:cover;
    min-height: 100vh;
}

.search-and-result {
    display:flex;
    flex-direction:row;
    align-items:flex-start;
    justify-content: space-between;
    width:100%;
    flex-wrap:wrap;
    @include mobile-only {
        flex-wrap:wrap;
        flex-direction: column;
    }
    & input {
        width: 80%;
        height: 4.5vw;
        font-size: 2.5vw;
        outline: none;
        border:none;
        background-color: transparent;
        color:white;
        box-sizing:border-box;
        text-align:center;

        @include mobile-only {
            font-size: 3vh;
            height: 6vh;
            width: 85%;
        }
    }
    & button {
        width: 4.5vw;
        height: 4.5vw;
        font-size: 2.5vw;
        color: rgb(255, 255, 255);
        background-color: rgba(0, 0, 0, 0.377);
        border-radius: 2.25vw;
        border: none;
        outline:none;
        box-sizing:border-box;
        border: 1px solid white;
        @include mobile-only {
            font-size: 3vh;
            height: 6vh;
            width: 6vh;
            border-radius: 3vh;
        }
        &:hover {
            color:orange;
        }
    };
    &__search-days-container {
        width: 100%;
        display: flex;
        flex-direction:row;
        flex-wrap:wrap;
        align-items: flex-start;
        justify-content:space-between;
        padding: 0 1vw;
        @include mobile-only{
            flex-direction:column-reverse;
        }
    }
}