@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.forecast {
    &__root-container {
        background-color:transparent;
        display:flex;
        flex-direction:row;
        flex-wrap:nowrap;
        align-items:center;
        background:rgba(36, 36, 36, 0.246);
        min-height: 60vh;
    }
    &__cards-container {
        display: flex;
        flex-direction:row;

    }
    &__card {
        display:flex;
        flex-basis: 25%;
        flex-direction: row;
        box-sizing: border-box;
        /*max-width: 15vw;*/
        padding-bottom: 1vw;
        box-sizing:border-box;
        overflow:hidden;
        color:white;
        min-height: 15vh;

        @include mobile-only {
            flex-basis: 95%;
            height: auto;
            font-size: 2vh;

            color:white;
            flex-direction:column;
            flex-wrap:nowrap;

            margin-top: 1vh;
            overflow:visible;
            &:nth-child(odd) {
                background-color: rgba(255, 255, 255, 0.103);
            }

        }

        &__data-container {
            width: 80%;
            display:flex;
            flex-direction: column;
            justify-content: center;
            @include mobile-only {
                flex-direction:row;
                width: 100%;
            }
        }

        &--current {
            width: 100%;
            color:white;
            background-color: transparent;
            overflow:hidden;
            height: max-content;
            flex-basis: 50%;
            @include mobile-only {
                flex-basis: 98%;
                width:98%;
                height: auto;
                margin:0;
                background-color: rgba(0, 134, 158, 0.822);

 
            }

        }
        &--selected {
            background: rgba(242, 192, 27, 0.068);


        }
        &__city {
            font-size: 2vw;
            text-align: left;
            width: max-content;
            @include mobile-only {
                font-size: 2.5vh;
            }
        }
        &__parameters-container {
            display:flex;
            flex-direction:row;
            justify-content: space-evenly;
            & > * {
                font-size: 1vw;
            }
            @include mobile-only {
                justify-content: space-around;
            }
        }
        
        &__time-weather {
            display:flex;
            flex-direction:column;
            flex-wrap:nowrap;
            align-items:center;
            justify-content:space-between;
            width: 35%;

            
            
            @include mobile-only {
                background-color: transparent;
                height: auto;
                align-items:flex-start;
                width:100%;
                border:none;
                flex-direction:row;

            }
            &__time {
                margin-right: 0;
                color:white;
                background-color:rgba(0, 0, 0, 0.603);
                width: 100%;
                font-size: 2vw;
                @include mobile-only {
                    font-size: 4vh;
                    margin-right:-5%;
                    z-index: 0;
                    color:rgb(255, 255, 255);
                    text-align:left;
                    padding: 0;
                    height: auto;
                    box-sizing:border-box;
                    text-shadow:2px 2px 2px rgb(148, 67, 1);



                }

            }
            &__error {
                font-size: 2vw;
                margin-right: 0;
                color:white;
                @include mobile-only {
                    font-size: 2.5vh;
                    margin-right:-5%;
                    z-index: 99;
                    color:rgb(255, 255, 255);
                    text-align:center;
                    padding: 0;
                    height: auto;
                    box-sizing:border-box;
                    text-shadow:2px 2px 2px rgb(148, 67, 1);
                    background-color: rgba(0, 0, 0, 0.603);
                    padding: 1vh;

                }

            }

            
            &__icon {
                font-size: 3.5vw;
                color:rgb(223, 223, 223);
                width:100%;
                margin-bottom:1vh;
                z-index:1;
                background:white;
                @include mobile-only {
                    font-size: 10vh;

                }
                &--Rain {
                    background: linear-gradient(to bottom, rgb(255, 255, 255), rgb(50, 135, 204));
                    color: transparent;
                    background-clip: text;  
                }
                &--Clouds {

                    background: linear-gradient(120deg, rgb(255, 255, 255), rgb(57, 79, 97));
                    color: transparent;
                    background-clip: text;  
                }
                &--Mist {
                    background: linear-gradient(120deg, rgb(255, 255, 255), rgb(57, 79, 97));
                    color: transparent;
                    background-clip: text;  
                }
                &--Clear {
                    background: linear-gradient(120deg, rgb(251, 255, 0), rgb(255, 196, 0));
                    color: transparent;
                    background-clip: text;  
                }
                &--Thunderstorm {
                    color: rgb(255, 51, 0);
                }
                &--Snow {
                    background: linear-gradient(120deg, rgb(255, 255, 255), rgb(0, 174, 255));
                    color: transparent;
                    background-clip: text;  
                }
                &--big {
                    font-size: 3vw;

                    padding-right:1vw;
                    text-align:center;

                    @include mobile-only {
                        font-size: 7vh;
                        height: auto;
                        margin-top:0;
                        
                    }
                }
            }
        }
        &__temp {
            display:flex;
            align-items:center;
            width:100%;
            margin-top:0.3vw;
            &__icon {

                width: 20%;
                color:red;  
                margin-right: 0.5vw; 
                text-align:center;
                @include mobile-only {
                    font-size: 2.5vh;
                    margin-right: 0.5vh; 


                }
            }
            &__value {

                width:70%;
                text-align:left;
                @include mobile-only {
                    font-size: 2.5vh;
                }
                &:after {
                    content: "°C";
                    margin-left: 0.5vw;
                }
            }
        }
        &__wind {
            display:flex;
            align-items:center;
            width:100%;
            margin-top:0.3vw;
            @include mobile-only {


            }
            &__icon {
                width:20%;
                color:rgb(0, 174, 255);  
                margin-right: 0.5vw; 
                text-align:center;
                @include mobile-only {
                    font-size: 2.5vh;
                    margin-right: 2vh;
                }
            }
            &__value {

                width:70%;
                text-align:left;
                @include mobile-only {
                    font-size: 2.5vh;
                }
                &:after {
                    content: "m/s";
                    margin-left: 0.5vw;
                    
                }
            }
        }
        &__wind-direction {
            display:flex;
            align-items:center;
            margin-top:0.3vw;
            width:100%;
            &__icon {

                width: 20%;
                color:rgb(0, 255, 191);
                text-align:center; 
                @include mobile-only {
                    font-size: 2.5vh;
                    margin-right: 2vh;
                }
            }
            &__value {
                text-align:left; 
                width: 70%;
                @include mobile-only {
                    font-size: 1.7vh;

                }
            }
        }

    }
    &__day-container {
        display:flex;
        align-items:center;
        justify-content:center;
        max-width: 100%;
        margin: 0;
        & > * {
            justify-content:flex-start;
        }
        &__date {
            font-size: 1.5vw;
            width: 10vw;
        }

    }
    &__day-forecasts {
        display:flex;
        flex-direction:row;
        flex-wrap:wrap;
        width:60vw;
        /*padding: 0 10%;*/
        justify-content:space-around;
        align-items:center;
        overflow:hidden;
        height: 60vh;
        justify-content:flex-start;
        padding-left: 5vw;
        @include mobile-only {
            padding-bottom: 30vh;
            width:100vw;
            justify-content:center;
            height: max-content;
            padding-left: 0vw;
        }

    }
    
}